import Vue from "vue";
import Vuex from "vuex";
import { getStor } from "@/helpers/storage";

let token = getStor("token") ? getStor("token") : null;

Vue.use(Vuex);

const state = {
  userInfo: {
    token: token
  }
};

const mutations = {
  login(state, token) {
    state.userInfo.token = token;
  }
};

const actions = {};

export default new Vuex.Store({
  state,
  actions,
  mutations
});
