/**
 * 存储内容到 LocalStorage ，比如 Token
 * @param {string} name localStorage 名称
 * @param {string} content localStorage 内容
 */
export const setStor = (name, content) => {
  if (!name) return;
  if (typeof content !== "string") {
    content = JSON.stringify(content);
  }
  window.localStorage.setItem(name, content);
};

/**
 * 获取 Localstorgae 内容
 * @param {string} name localstorage 名称
 */
export const getStor = name => {
  if (!name) return;
  return window.localStorage.getItem(name);
};

/**
 * 删除一个 localstorage
 * @param {string} name localstorage 名称
 */
export const rmStor = name => {
  if (!name) return;
  window.localStorage.removeItem(name);
};
