import Vue from "vue";
import Router from "vue-router";
import store from "@/store";
import iView from "iview";
import { setTitle } from "@helpers/title";

Vue.use(Router);

const login = r =>
  require.ensure([], () => r(require("@view/login/Login")), "login");

const home = r =>
  require.ensure([], () => r(require("@view/home/Home")), "login");

const about = r =>
  require.ensure([], () => r(require("@view/about/About")), "about");

const dashboard = r =>
  require.ensure(
    [],
    () => r(require("@view/analytics/Dashboard")),
    "dashboard"
  );

const devices = r =>
  require.ensure([], () => r(require("@view/devices/Devices")), "devices");

const animals = r =>
  require.ensure([], () => r(require("@view/animals/Animals")), "animals");

let router = new Router({
  mode: "history",
  base: process.env.BASE_URL,
  routes: [
    {
      path: "/",
      name: "login",
      meta: {
        auth: false
      },
      component: login
    },
    {
      path: "/dashboard",
      component: home,
      name: "home",
      meta: {
        title: "后台",
        auth: true
      },
      children: [
        {
          path: "",
          component: dashboard,
          meta: {
            title: "数据统计",
            auth: true
          }
        },
        {
          path: "/about",
          component: about,
          meta: {
            title: "关于",
            auth: true
          }
        },
        {
          path: "/devices",
          component: devices,
          meta: {
            title: "设备管理",
            auth: true
          }
        },
        {
          path: "/animals",
          component: animals,
          meta: {
            title: "动物管理",
            auth: true
          }
        }
      ]
    }
  ]
});

router.beforeEach((to, from, next) => {
  iView.LoadingBar.start();
  const { name, meta } = to;
  const { auth } = meta;
  let userLogin = store.state.userInfo.token;

  if (name === "login" && userLogin) {
    return next({
      name: "home"
    });
  }
  /**
   * 如果用户前往登陆，就直接放行
   */
  if (name === "login" && !userLogin) {
    next();
  }
  const needLogin = auth && !userLogin;
  if (needLogin) {
    return next({
      name: "login"
    });
  }
  next();
});

router.afterEach(to => {
  setTitle(to, router.app);
  iView.LoadingBar.finish();
  window.scrollTo(0, 0);
});

export default router;
